/* 공통 */
html, body {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}
* {
  margin: 0;
  padding: 0;
}
.flex {
  display: flex;
}
.bg-black {
  background: #333;
  color: #fff;
}

/* 타이틀 */
.title {
  width: 100%;
  padding: 4rem 2rem;
  box-sizing: border-box; 
}

/* 콘텐츠 */
.content-box {
  width: 100%;
}
.content {
  padding: 3rem;
  border-bottom: 0.1rem solid #333;
}
.content .content-title {
  cursor: pointer;
}
.content .content-title p {
  display: inline-block;
  margin: 0 0.5rem 0 3rem;
}
.content .text {
  margin: 1.5rem 0 0.3rem;
}
.content .date {
  font-size: 1.3rem;
  color: #686868;
}

/* dialog */
.dialog {
  margin: 2rem;
  padding: 2rem;
  background: #eee;
}


/* footer */
.footer {
  width: 100%;
  line-height: 15rem;
  background: #333;
  color: #fff;
  padding: 3rem 5rem;
  box-sizing: border-box;
}
